/* global console, document, Excel, Office */
/* global bootstrap */
import { convertToJSON, getDataTable } from "../commons/common.js";

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    const outputElement = document.getElementById("output");
    if (outputElement) {
      (async () => {
        const data = await loadData();
        const jsonData = convertToJSON(data);
        outputElement.innerHTML = generateTableHTML(jsonData);
      })();
    } else {
      console.error('Element with id "output" not found.');
    }
  }
});

function generateTableHTML(data) {
  if (!data || data.length === 0) return "<p>No data available</p>";

  let tableHTML = "<table class='table table-bordered'><thead><tr>";

  // Tạo header
  const baseHeaders = ["STT", "Account", "Fullname", "Katakana", "Email", "Role"];
  tableHTML += baseHeaders.map((header) => `<th>${header}</th>`).join("");
  tableHTML += "</tr></thead><tbody>";

  // Tạo các dòng dữ liệu
  for (const row of data) {
    tableHTML += "<tr>";
    for (const header of baseHeaders) {
      tableHTML += `<td>${row[header] || ""}</td>`;
    }
    tableHTML += "</tr>";
  }

  tableHTML += "</tbody></table>";
  return tableHTML;
}

async function loadData() {
  const data = await getDataTable("Resource", "TblResource");
  return data;
}
